import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DebtsContainer} from './debts.container';
import {DebtCustomersComponent} from './pages/debt-customers/debt-customers.component';
import {DebtSuppliersComponent} from './pages/debt-suppliers/debt-suppliers.component';

const routes: Routes = [
  {
    path: '',
    component: DebtsContainer,
    children: [
      {
        path: 'customers',
        component: DebtCustomersComponent
      },
      {
        path: 'suppliers',
        component:  DebtSuppliersComponent
      }
    ]
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DebtsRoutingModule { }
